import React, { useState } from 'react';
import { isLightColor } from '../utils';


export function GetElementImage({element}){
  try {
    return <img className="card-img-top" 
                style = {{
                  width: "100px",
                  height: "100px",
                  position: "relative",
                  overflow: "hidden",
                  borderRadius: "50%", 
                  margin : "auto",
                }} 
                src={require(`../assets/img/element/${element.symbol}.jpg`).default} 
                alt=""/>
  }catch {
    return (
      <div className="card-img-top mx-auto" 
          style = {{
            width: "100px",
            height: "100px",
            position: "relative",
            overflow: "hidden",
            borderColor : "#000000",
            borderWidth : "0.6",
            borderStyle : "dashed",
            borderRadius: "50%", 
            margin : "auto",
          }} 
        >
        <svg   viewBox = "0 0 50 50">
          <rect  width = "50" height = "50" fill = {`${element.cpk_color}`}></rect>
          <text x = "24" y = "35" textAnchor = "middle" fill = {isLightColor(element.cpk_color || "#000000") ? "#000000" : "#FFFFFF"} fontSize = "24">{element.symbol}</text>
        </svg>
      </div>
    )
  }
}

export function GetSpectrumImage({element}){
  try {
    return <img className="card-img-top" 
                style = {{
                  position: "relative",
                  overflow: "hidden",
                  borderRadius: "1%", 
                  margin : "auto",
                }} 
            src={require(`../assets/spectrum/${element.symbol}.gif`).default} 
            alt=""/>
  }catch{
    return <div></div>
  }
}