import { json } from 'd3';
import { useSelector } from 'react-redux';
import {useParams} from 'react-router-dom';

import { getElementFromElements } from '../utils';
import {ElementDetails} from '../components/ElementDetails'


export function Element({name}){
  const ele_param = useParams().element;
  const elements = useSelector(state=>state.elements);
  console.log("elelemnts : ", elements);
  var element = getElementFromElements(ele_param, elements.data);
  console.log("element : ", element, ele_param, typeof ele_param, !isNaN(ele_param) && !isNaN(parseInt(ele_param)));
  return elements.loading ? <>Loading ...</>: <ElementDetails element = {element}></ElementDetails>
}