
import "../assets/css/icon.css"
import EarthCrustSVG from "../assets/svg/EarthCrust.svg"

export function SeaWave(){
  return (
    <div style = {{position : "absolute", width : "100%", bottom : "0", borderRadius : "12"}}>
      <svg className="waves" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 24 150 28" preserveAspectRatio="none" shapeRendering="auto">
      <defs>
      <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
      </defs>
      <g className="parallax">
      <use xlinkHref="#gentle-wave" x="48" y="0" fill="rgba(0,0,255,0.7" />
      <use xlinkHref="#gentle-wave" x="48" y="3" fill="rgba(0,255,255,0.5)" />
      <use xlinkHref="#gentle-wave" x="48" y="5" fill="rgba(0,200,255,0.3)" />
      {/* <use xlinkHref="#gentle-wave" x="48" y="7" fill="#555EFF" /> */}
      </g>
      </svg>
    </div>

  )
}

export function EarthCrust(){
  return (
    <div style = {{backgroundPosition : "center", width : "100%", height : "100px"}}>
      <img src = {EarthCrustSVG} alt = "Curst"  width = "100%" height = "100"></img>
    </div>
    
  )
}