import {useEffect, useRef} from 'react'
import * as lodash from 'lodash'
import * as d3 from "d3";

import { ResponsivePie, Pie } from '@nivo/pie'
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
export function PieChart({ data /* see data tab */ , ...other}){
  console.log(data);
   return <div style = {{width : "100%", height : "100%", position : "relative"}}>
      <ResponsivePie
        data={data}
        margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
        innerRadius={0.5}
        padAngle={0.7}
        cornerRadius={3}
        colors={{ scheme: 'nivo' }}
        borderWidth={1}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
        radialLabel = {d=>`${other.symbol}-${d.id}`}
        radialLabelsSkipAngle={3}
        radialLabelsTextColor={{ from: 'color' }}//"inherit"
        radialLabelsLinkColor={{ from: 'color' }}
        radialLabelsLinkStrokeWidth = {3}
        // enableSliceLabels = {false}
        sliceLabel = {d=>`${d.value} %`}
        sliceLabelsSkipAngle={20}

        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: 'rgba(255, 255, 255, 0.3)',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: 'rgba(255, 255, 255, 0.3)',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        
        legends={[
            {
                anchor: 'bottom',
                direction: 'row',
                justify: false,
                translateX: 0,
                translateY: 56,
                itemsSpacing: 0,
                itemWidth: 86,
                itemHeight: 18,
                itemTextColor: '#999',
                itemDirection: 'left-to-right',
                itemOpacity: 1,
                symbolSize: 18,
                symbolShape: 'circle',
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemTextColor: '#000'
                        }
                    }
                ]
            }
        ]}
    />
   </div>
}