/**
 * s, p, d, f, g, h, i, k, l,
 * @param {Array} shells e.g Pt : [2, 8, 18, 32, 17, 1]
 */
export function getOrbitalsFromElectronShells(shells = []){
  const result = {}
  shells.forEach((e_count, n)=>{
    if (e_count === 2 * (n + 1) * (n + 1) ) return;
    if(e_count <= 2){ // s orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : e_count,
        t : e_count
      }
    }if(e_count <= 8 && e_count > 2){ // p orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : e_count - 2,
        t : e_count
      }
    }if(e_count <= 18 && e_count > 8){ // d orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : e_count - 8,
        t : e_count
      }
    }if(e_count <= 32 && e_count > 18){ // f orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : 10
      }
      result.f = {
        n : n + 1,
        l : "f",
        e : e_count - 18
      }
    }if(e_count <= 50 && e_count > 32){ // g orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : 10
      }
      result.f = {
        n : n + 1,
        l : "f",
        e : 14
      }
      result.g = {
        n : n,
        l : "g",
        e : e_count - 32,
      }
    }
  })
  return result;
}
export function getElementFromElements(ele_param, elements){
  var element = null;
  console.log("elements : ", elements);
  try {
    if(!isNaN(ele_param) && !isNaN(parseInt(ele_param))) 
      element = elements.filter(element => element.atomic_number.toString() === ele_param)[0];
    else if (typeof ele_param === 'string' && ele_param.length < 3){
      element = elements.filter(element=>element.symbol.toLowerCase() === ele_param.toLowerCase() )[0]
    }
    else if (typeof ele_param === 'string'){
      element = elements.filter(element => element.name.toLowerCase() === ele_param.toLowerCase())[0];
    }
  }catch (err){

  }
  return element;
}
export const color2shade = (col, amt) => {
  // console.log("col, amt ", col, amt);
  col = col.replace(/^#/, '')
  if (col.length === 3) col = col[0] + col[0] + col[1] + col[1] + col[2] + col[2]

  let [r, g, b] = col.match(/.{2}/g);
  ([r, g, b] = [parseInt(r, 16) + amt, parseInt(g, 16) + amt, parseInt(b, 16) + amt])

  r = Math.max(Math.min(255, r), 0).toString(16)
  g = Math.max(Math.min(255, g), 0).toString(16)
  b = Math.max(Math.min(255, b), 0).toString(16)

  const rr = (r.length < 2 ? '0' : '') + r
  const gg = (g.length < 2 ? '0' : '') + g
  const bb = (b.length < 2 ? '0' : '') + b

  return `#${rr}${gg}${bb}`
}

export function component2Hex(c) {
  var hex =typeof c === 'string' ? parseInt(c).toString(16) : c.toString(16);
  return hex.length === 1 ? "0" + hex : hex;
}

export function rgb2Hex(r, g, b) {
  // console.log(r, g, b, "#" + component2Hex(r) + component2Hex(g) + component2Hex(b));
  return "#" + component2Hex(r) + component2Hex(g) + component2Hex(b);
}

export function rgbString2Hex(rgb, invert = false) {
  // console.log("color : ", rgb);
  if (invert) return rgb2Hex(...rgb.replace(/rgb\(|\)|rgba\(|\)|\s/gi, '').split(',').map(c=>255 - c))
  return rgb2Hex(...rgb.replace(/rgb\(|\)|rgba\(|\)|\s/gi, '').split(','))
}

export function getGradientFromColor(color = "#FFFF00", shift = 0) {
  // console.log("color : ", color);
  return `linear-gradient(to bottom, ${color} 0%, ${color2shade(color, shift)} 10%)`
}



export function isLightColor(color) {
  var r, g, b;
  // Check the format of the color, HEX or RGB?
  if (color.match(/^rgb/)) {

    // If HEX --> store the red, green, blue values in separate variables
    color = color.match(/^rgba?\((\d+),\s*(\d+),\s*(\d+)(?:,\s*(\d+(?:\.\d+)?))?\)$/);

    r = color[1];
    g = color[2];
    b = color[3];
  } 
  else {

    // If RGB --> Convert it to HEX: http://gist.github.com/983661
    color = +("0x" + color.slice(1).replace( 
      color.length < 5 && /./g, '$&$&'
    )
             );

    r = color >> 16;
    g = color >> 8 & 255;
    b = color & 255;
  }

  // HSP (Highly Sensitive Poo) equation from http://alienryderflex.com/hsp.html
  var hsp = Math.sqrt(
    0.299 * (r * r) +
    0.587 * (g * g) +
    0.114 * (b * b)
  );

  // Using the HSP value, determine whether the color is light or dark
  return hsp > 127;
}




export function vw2px(value) {
  var w = window,
    d = document,
    e = d.documentElement,
    g = d.getElementsByTagName('body')[0],
    x = w.innerWidth || e.clientWidth || g.clientWidth,
    y = w.innerHeight|| e.clientHeight|| g.clientHeight;

  var result = (x*value)/100;
  // document.getElementById("result_vw_px").innerHTML = result;  // affichage du résultat (facultatif)
  return result;
}

export function vh2px(value) {
  var w = window,
    d = document,
    e = d.documentElement,
    g = d.getElementsByTagName('body')[0],
    x = w.innerWidth || e.clientWidth || g.clientWidth,
    y = w.innerHeight|| e.clientHeight|| g.clientHeight;

  var result = (y*value)/100;
  // document.getElementById("result_vh_px").innerHTML = result;  // affichage du résultat (facultatif)
  return result;
}
export const groupby = (x,f)=>x.reduce((a,b)=>{
  if(!Array.isArray(a[f(b)])) a[f(b)] = []; 
  a[f(b)].push(b);
  return a;
},{});


