import { SemiFilledOrbit, Atom } from "./Atom";
import { IonicRadiusGraph } from "./Graph";
import { EarthCrust, SeaWave } from "./Icon";
import { PieChart } from "./PieChart";

import * as d3 from "d3"
import { useSelector } from "react-redux";

import * as utils from '../utils'
import { GetElementImage, GetSpectrumImage } from "./Image";



function getElementImage(element){
  try {
    return require(`../assets/img/element/${element.symbol}.jpg`).default
  }catch{
    <svg viewBox = "0 0 100 100">
      <text x = "50" y = "50" textAnchor = "middle" fontSize = "24">{element.symbol}</text>
    </svg>
  }
}
export function ElementDetails({element}){
  const maxprops = useSelector(state=>state.elements.maxprops);
  const minprops = useSelector(state=>state.elements.minprops);
  function getElementStyle(property, invert = false, head = false){
    // console.log(" --------- ", property, invert, head);
    var textcolor = utils.rgbString2Hex(getTextColor(property, element[property]));
    if (invert && !head ) 
      return {
        color : getBgColor(property, element[property]),
      }
    else if (invert || head) return {
      // backgroundColor: "#000000 !important",
      backgroundColor: textcolor,//getTextColor(property, element[property]),
      color : utils.isLightColor(textcolor) ? "#000000" : "#FFFFFF"//getBgColor(property, element[property]),
    }
    else return {
      // backgroundColor : getBgColor(property, element[property]),
      color : getTextColor(property, element[property]),
    }
  }
  function componentToHex(c) {
    var hex = c.toString(16);
    return hex.length === 1 ? "0" + hex : hex;
  }

  function rgbToHex(r, g, b) {
      return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
  }

  function getTextColor(name, value){ //property name and its value
    try {
      // console.log("percent : ", (value - minprops[name]) / (maxprops[name] - minprops[name]), d3.interpolateRdYlBu(0));
      // console.log(minprops, maxprops, d3.interpolateRdYlBu((value - minprops[name]) / (maxprops[name] - minprops[name]) * .8 + .1).invert());
      return d3.interpolateTurbo(Math.abs((value - minprops[name]) / (maxprops[name] - minprops[name]) + 0.4));
    }catch {
      return "#000000"
    }
  }
  function getBgColor(name, value){
    var color = d3.interpolateTurbo(Math.abs((value - minprops[name]) / (maxprops[name] - minprops[name]) + 0.4));
    var rgb = color.replace(/rgb\(|\)|rgba\(|\)|\s/gi, '').split(',').map(c=>255 - c);
    // console.log("rgb : " , rgb, color, rgbToHex(...rgb));
    return rgbToHex(...rgb)
    // return "#FFFFFF"

  }
  return (
    <div className = "container">
      <div className = "row">
        <div className = "col col-md-3 border">
          {/* Sidebar (Left)  */}
          <div className = "sticky-top">
            <div className = " "></div>
            <div class="card pt-2 border-0" >
              <GetElementImage element = {element}></GetElementImage>
              <div class="card-body">
                <h5 class="card-title h3 fw-bold">{element.name}</h5>
              </div>
            </div>
            <div id = "Atom" className = "text-left" style = {{textAlign :"left"}}>
              <div className = "h5 fw-bold "> Atom Shells</div>
              <Atom title = {element.symbol} shells = {element.shells} color = {element.cpk_color}></Atom>
            </div>
            <div className = "electronic-conf" style = {{textAlign :"left"}}>
              <div className = "h5 fw-bold"> Electronic Configuration</div>
              <div class="alert alert-warning fw-bold" role="alert">
                {element.econf}
              </div>
            </div>
          </div>
        </div>
        <div className = "col col-md-9 col-md-offset-3 ">
          {/* Details (Right)  */}
          <GetSpectrumImage element = {element}></GetSpectrumImage>
          

          <div class="w3-panel w3-light-grey">
            <div class="w3-panel w3-leftbar">
              <p><i class="fa fa-quote-right h3">&#8252;</i><br/>
              <i class="w3-serif w3-xlarge">{element.description}</i></p>
            </div>
          </div>
          <div className = "row card-group mb-3">
            {/* <div className = "col-md-2 px-0"> */}
              <div class="col-md-2 px-0 card text-white bg-primary mb-0" >
                <div class="card-header fw-bold">Symbol</div>
                <div class="card-body">
                  <h5 class="card-title h2">{element.symbol}</h5>
                </div>
              </div>
            {/* </div> */}
            {/* <div className = "col-md-2 px-0"> */}
              <div class="col-md-2 px-0 card text-dark bg-warning mb-0" >
                <div class="card-header fw-bold">Atomic No.</div>
                <div class="card-body">
                  <h5 class="card-title h2">{element.atomic_number}</h5>
                </div>
              </div>
            {/* </div> */}
            {/* <div className = "col-md-2 px-0"> */}
              <div class="col-md-2 px-0 card text-white bg-danger mb-0" >
                <div class="card-header fw-bold">Period</div>
                <div class="card-body">
                  <h5 class="card-title h2">{element.period}</h5>
                </div>
              </div>
            {/* </div> */}
            {/* <div className = "col-md-2 px-0"> */}
              <div class="col-md-2 px-0  card text-dark bg-info mb-0" >
                <div class="card-header fw-bold">Group</div>
                <div class="card-body">
                  <h5 class="card-title h2">{element.group_id}</h5>
                </div>
              </div>
            {/* </div> */}

            <div class="col-md-2 px-0 card text-dark bg-light mb-0" >
                <div class="card-header fw-bold">Block</div>
                <div class="card-body">
                  <h5 class="card-title h2">{element.block}</h5>
                </div>
            </div>
          </div>
          <div className = "row ">
            <div class="col-md-6 card mb-3" style = {{backgroundColor : "rgb(0, 54, 73)"}}>
                <div className = "row">
                  <div className = "col-md-12 px-0">
                    <div class="card-header bg-dark text-white fw-bold">Abundance in Crust</div>
                  </div>
                </div>
                <div class="row g-0 text-light" style = {{backgroundColor : "rgb(0, 54, 73)"}}>
                  <div class="col-md-4">
                    <EarthCrust></EarthCrust>
                  </div>
                  <div class="col-md-8">
                    <div class="card-body">
                      <h5 class="card-title h2">{element.abundance_crust / 1e4 } %</h5> 
                    </div>
                  </div>
                </div>
            </div>
            <div class="col-md-6 px-0 card text-dark bg-light mb-3" >
                <SeaWave style = {{position : "", width : "100%", height : "60%"}}></SeaWave>
                <div className = "card bg-transparent" style = {{zIndex : "2"}}>
                  <div class="card-header bg-dark text-white fw-bold">Abundance in Sea</div>
                  <div class="card-body">
                    <h5 class="card-title h2">{(element.abundance_sea || 0.0).toFixed(5) } mg / L</h5>
                    {/* <SeaWave style = {{position : "absoulute"}}></SeaWave> */}

                  </div>
                </div>
                  
            </div>
          </div>

          {/* ROW 1 */}
          <div className = "row border">
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("melting_point")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("melting_point", true, true)}}>Melting Point</div>
                <div class="card-body text-primary">
                  <h5 class="card-title fw-bold"  style = {{...getElementStyle("melting_point")}}>{element.melting_point} K </h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
              
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("melting_point")}}>
                <div class="card-header light-bg fw-bold h6" style = {{...getElementStyle("melting_point", true, true)}}>Boiling Point</div>
                <div class="card-body text-primary" >
                  <h5 class="card-title fw-bold" style = {{...getElementStyle("melting_point")}} >{element.boiling_point} K </h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>

            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("density")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("density", true, true)}}>Density</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("density")}}>{element.density} g/cm<sup>3</sup></h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("atomic_radius")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("atomic_radius", true, true)}}>Atomic Radius</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("atomic_radius")}}>{element.atomic_radius} nm</h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
          </div>
          {/* ROW 1 END */}
         
          {/* ROW 2 */}
          <div className = "row border">
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("atomic_weight")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("atomic_weight", true, true)}}>Atomic Mass</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("atomic_weight")}}>{element.atomic_weight} </h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
              
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("fusion_heat")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("fusion_heat", true, true)}}>Fusion Heat</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("fusion_heat")}}>{element.fusion_heat} </h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>

            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("specific_heat")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("specific_heat", true, true)}}>Specific Heat</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("specific_heat")}}>{element.specific_heat} J kg<sup>−1</sup> K<sup>-1</sup></h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("thermal_conductivity")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("thermal_conductivity", true, true)}}>Thermal Conductivity</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("thermal_conductivity")}}>{element.thermal_conductivity} W m<sup>-1 </sup>K<sup>-1 </sup></h5>
                  {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                </div>
              </div>
            </div>
          </div>
          {/* ROW 2 END */}
          
          {/* ROW 3 */}
          {/* <div className = "row border">
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("melting_point")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("melting_point", true, true)}}>Melting Point</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("melting_point")}}>{element.melting_point} K </h5>
                </div>
              </div>
            </div>
              
            <div className = "col-sm-12 col-md-3 px-0 border">
              <div class="card border-light" style={{ ...getElementStyle("boiling_point")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("boiling_point", true, true)}}>Boiling Point</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("boiling_point")}}>{element.boiling_point} K </h5>
                </div>
              </div>
            </div>

            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("density")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("density", true, true)}}>Density</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("density")}}>{element.density} g/cm<sup>3</sup></h5>
                </div>
              </div>
            </div>
            <div className = "col-sm-12 col-md-3 px-0 border">   
              <div class="card border-light" style={{ ...getElementStyle("atomic_radius")}}>
                <div class="card-header fw-bold h6" style = {{...getElementStyle("atomic_radius", true, true)}}>Atomic Radius</div>
                <div class="card-body text-primary">
                  <h5 class="card-title" style = {{...getElementStyle("atomic_radius")}}>{element.atomic_radius} nm</h5>
                </div>
              </div>
            </div>
          </div> */}
          {/* ROW END 3 */}
          {/* <IonicRadiusGraph element = {element}></IonicRadiusGraph> */}
          {/* ROW 4 - ISOTOPES */}
          <div className = "row">
            <div className = "col-12 col-md-5">
              <PieChart
                style = {{height : "100%", width : "100%"}}
                data = {element.isotopes.map((v)=>({
                  html : `<span>${element.symbol}<sup>${element.mass}</sup></span>`, 
                  label : `${element.symbol}-${v.mass_number}`,
                  value : v.abundance,
                  id : v.mass_number,
                }
                ))}
                // data = {data}
                symbol = {element.symbol}
              />
            </div>
            <div className = "col-12 col-md-7 p-4 col-sm-offset-2">
              <h1 className = "h3 fw-bold">{element.name} Isotopes</h1>
              <table className="table text-center table-hover table-striped">
                <thead>
                  <tr className = "table-secondary">
                    <th scope="col align-middle">Name</th>
                    <th scope="col align-middle">Abundance</th>
                    <th scope="col align-middle">Mass</th>
                    <th scope="col align-middle">Mass Number</th>
                    <th scope="col">Half Life<br/><small>(in secs)</small></th>
                  </tr>
                </thead>
                <tbody>
                  {
                    element.isotopes.map((isotope)=>{
                      return <tr>
                        <th scope="row">{element.symbol}<sup>{isotope.mass_number}</sup></th>
                        <td>{isotope.abundance || "-"}</td>
                        <td>{isotope.mass || "-"}</td>
                        <td>{isotope.mass_number || "-"}</td>
                        <td className = {isotope.is_radioactive ? "table-danger" : "table-success"}>{isotope.half_life || "-"}</td>
                      </tr>
                    })
                  }
                </tbody>
              </table>
            </div>
          </div>
          {/* ROW 4 ENDS */}
          <div style = {{marginTop : "40px"}}></div>
        </div>
      </div>
    </div>
  )
}