import * as actions_types from "../constants/action-types"
import * as lodash from "lodash"

export const elements = (state = {}, action)=>{
  switch(action.type){
    case actions_types.GET_ALL_COMPONENTS_START:
      return {...state, loading : true, data : null};
    case actions_types.GET_ALL_COMPONENTS_SUCCESS:
      var maxprops = ["melting_point", 'boiling_point', 'density', 'atomic_radius', 'atomic_weight', 'fusion_heat', 'specific_heat', 'thermal_conductivity'].reduce(
        (obj, item) => {
          var item_max_val = lodash.max(action.data.map(v=>v[item]))
          return Object.assign(obj, { [item]: item_max_val});
        }, {}
      );
      var minprops = ["melting_point", 'boiling_point', 'density', 'atomic_radius', 'atomic_weight', 'fusion_heat', 'specific_heat', 'thermal_conductivity'].reduce(
        (obj, item) => {
          var item_min_val = lodash.min(action.data.map(v=>v[item]))
          return Object.assign(obj, { [item]: item_min_val});
        }, {}
      );
      return {...state, maxprops, minprops, loading : false, data : action.data};
    case actions_types.GET_ALL_COMPONENTS_FAILURE:
      return {...state, loading : false, data : null};
    default :
      return state
  }
}