import logo from './logo.svg';
import './App.css';

import { Provider, useDispatch } from 'react-redux'

import {Route, Redirect, Switch, useLocation} from 'react-router-dom';

import * as async_actions from "./actions/async_actions"
// import {Element} from './components/Element'
import { PeriodicTable } from './components/PeriodicTable';
import {store} from "./store"
import { GroupIcon, SemiFilledOrbit } from './components/Atom';
import { Home } from './pages/Home';
import { Element } from './pages/Element';

import "./assets/css/bootstrap.css"
function App() {
  const dispatch = useDispatch();
  const location = useLocation();
  dispatch(async_actions.getAllElements());
  return (
    <div className = "App">
       <Switch location={location}>
         <Route
          exact
          path = "/"
          render = {Home}
          key = "0"
         >
         </Route>
         <Route
          exact
          path = "/element/:element"
          render = {({match}) => <Element/>}
          key = "0"
         >
         </Route>
       </Switch>
      {/* <GroupIcon title = "Group 8" shells = {[2, 8, 14, 2]}></GroupIcon> */}
    </div>
  );
}

export default App;
